import React from "react";
import { connect } from "react-redux";
import { Route, Switch, withRouter } from "react-router-dom";
import { Container, Row } from "reactstrap";
import { getAdminApproveListing, updateApproveListing } from "state/AdminDashboard/Epic";
import { selectList } from "state/AdminDashboard/Selectors";
import DashboardMainMenu from "../../components/DashboardMainMenu";
import BaseIndexPage from "../../components/HOC/BaseIndexPage";
import SaveSearches from "./Components/SaveSearches";
import VerticalMenu from "./Components/VerticalMenu";
import Listings from "./Components/listings";


const Index = ({ match: { path },getAdminApproveListing,approvelist,onSubmitItem,  ...props }) => {
  return (
    <div className=" mg-top-90 ">
      <DashboardMainMenu />
      <Container>
        <Row className="py-4">
          <VerticalMenu />
          <Switch>
            {/* <Route exact path={path}>
              <Dashboard />
            </Route> */}
            <Route exact path={`${path}`}>
              <Listings onSubmitItem={onSubmitItem} list={approvelist} getAdminApproveListing={getAdminApproveListing} />
            </Route>
            <Route exact path={`${path}/users`}>
              {/* <SaveSearches /> */}
              <div>Coming Soon</div>
            </Route>
           
          </Switch>
        </Row>
      </Container>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    noHeader: true,
    approvelist: selectList(state),
    noBanner: true,
  };
};

const mapDispatchToProps = (dispatch) => ({
  update: (id, params) => dispatch(updateApproveListing(id, params)),
  getAdminApproveListing: (params) => dispatch(getAdminApproveListing(params)),
});

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(BaseIndexPage(Index)));
